@import "../../sass/main.sass"
@import "../../components/header/header.sass"
@import "../../components/footer/footer.sass"
@import "../../components/field/field.sass"
@import "../../components/DropDown/dropdown.sass"
@import "../../components/range-slider/range-slider.sass"
@import "../../components/checkbox/checkbox.sass"
@import "../../components/rate/rate.sass"
@import "../../components/pagination/pagination.sass"
@import "../../components/rooms-item/rooms-item.sass"

.main
  margin-top: 30px
  margin-bottom: 70px

  &__container
    @include container
    display: grid
    justify-content: space-between
    grid-template-columns: 266px 1fr
    grid-column-gap: 60px

    @media screen and ( max-width: 910px )
      grid-template-columns: 1fr

.rooms-filter

  @media screen and ( max-width: 910px )
    display: none
    &__item
      .js-Calendar
        left: 50%
        transform: translateX(-50%)
        top: 80px

  &__values
    position: absolute
    right: 0
    top: 0
    font-size: 13px
    color: $accent50

  &__item
    margin-bottom: 30px
    position: relative

    .datepicker-inline
      .js-Calendar
        left: 290px
        width: 318px

    &:last-child
      margin-bottom: 0
    &:nth-child(2)
      margin-bottom: 29px
    &:nth-child(3)
      margin-bottom: 15px
    &:nth-child(4)
      margin-bottom: 23px

  &__field
    margin-bottom: 20px
    &:last-child
      margin-bottom: 0

  &__checkbox
    margin-top: 16px



// Сетка для отображения номеров

.rooms
  &__grid
    margin-top: 20px
    display: grid
    grid-template-columns: repeat(auto-fill, minmax(270px, 1fr))
    grid-column-gap: 12px
    grid-row-gap: 15px

  &__offer
    @include h1

  &__pagination
    margin-top: 23px
    margin-right: 12px

.js-show-filters
  display: none
  font-size: 14px
  background-image: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%)
  color: #fff
  border-radius: 3px
  padding: 5px 15px
  cursor: pointer

  @media screen and ( max-width: 910px )
    display: inline-block
    margin-left: 20px

  @media screen and ( max-width: 735px )
    display: block
    text-align: center
    margin-top: 15px
    margin-left: 0

